body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  font-family: 'Press Start 2P', cursive;
 
}



.Board {
    position: relative;
    margin: 0 auto;
    background-color:yellow;
    background-image:
    -webkit-linear-gradient(rgb(23, 228, 108) 1px, transparent 1px),
    -webkit-linear-gradient(left, rgb(23, 228, 108) 1px, transparent 1px);
    background-image:
    linear-gradient(rgb(23, 228, 108) 1px, transparent 1px),
    linear-gradient(90deg, rgb(23, 228, 108) 1px, transparent 1px);
}

.Cell {
    background: fuchsia;
    position: absolute;
}

.controls {
    margin-top: 20px;
}

.button {
    margin-left: 12px;
    cursor: pointer;
    border-radius: 50%;
}
ol {
margin: 0 auto;
width: 700px;
}

.Rules h4 {
    text-align: center;
}

.Rules {
    border-top: 2px dotted purple;
    font-family: 'Coda', cursive;
    font-size: 20px;
    text-align: left;
}



.About {
    border-top: 2px dotted purple;
    font-family: 'Coda', cursive;
    font-size: 20px;
}
